// import Home from "@/pages/Home";
import Login from "@/pages/Login";
import Register from "@/pages/Register";
import Search from "@/pages/Search";
import Detail from "@/pages/Detail";
import AddCartSuccess from '@/pages/AddCartSuccess';
import ShopCart from '@/pages/ShopCart';
import Trade from '@/pages/Trade';
import Pay from '@/pages/Pay'
import PaySuccess from '@/pages/PaySuccess'
import Center from '@/pages/Center'
import MyOrder from '@/pages/Center/myOrder';
import GroupOrder from '@/pages/Center/groupOrder';
export default
    [
        {
            path: "/center",
            component: Center,
            name: 'center',
            meta: {
                show: true
            },
            //二级路由
            children: [
                {
                    path: 'myorder',
                    component: MyOrder
                },
                {
                    path: 'grouporder',
                    component: GroupOrder
                },
                {
                    path: '/center',
                    redirect: '/center/myorder'

                }
            ],

        },
        {
            path: "/paysuccess",
            component: PaySuccess,
            name: 'paysuccess',
            meta: {
                show: true
            },

        },
        {
            path: "/pay",
            component: Pay,
            name: 'pay',
            meta: {
                show: true
            },
            //路由独享首位
            beforeEnter: (to, from, next) => {
                if (from.path == '/trade') {
                    next()
                } else {
                    next(false)
                }
            }
        },
        {
            path: "/trade",
            component: Trade,
            name: 'trade',
            meta: {
                show: true
            },
            //路由独享首位
            beforeEnter: (to, from, next) => {
                if (from.path == '/shopcart') {
                    next()
                } else {
                    next(false)
                }
            }
        },
        {
            path: "/shopcart",
            component: ShopCart,
            name: 'shopcart',
            meta: {
                show: true
            }
        },
        {
            path: "/addcartsuccess",
            component: AddCartSuccess,
            name: 'addcartsuccess',
            meta: {
                show: true
            }
        },
        {
            path: "/detail/:skuid",
            component: Detail,
            meta: {
                show: true
            }
        },
        {
            path: "/home",
            //路由懒加载
            component: () => import("@/pages/Home"),
            meta: {
                show: true
            }
        },
        {
            path: "/login",
            component: Login,
            meta: {
                show: false
            },
        },
        {
            path: "/register",
            component: Register,
            meta: {
                show: false
            },
        },
        {
            path: "/search/:keyword?",
            component: Search,
            meta: {
                show: true
            },
            name: "search",
            props: ($route) => ({ keyword: $route.params.keyword, k: $route.query.k }),
        },
        {
            path: "*",
            redirect: "/home"
        },
    ]
